<template>
    <div class="newsdetail-layout" v-show="isShowAll">
        <header class="newsdetail-header-bar clearfix" v-show="!isWeiXinBowser">
            <a onclick="window.history.go(-1);" class="login-back-btn"></a>
            <h2 class="new-detail-t">{{newDetailObj.title}}</h2>
        </header>
        <main class="news-main-box" :class="{'news-main-box-bower':!isWeiXinBowser}" >
            <section class="news-detail-box">
                <div class="tiezie-detail-header" :class="{'tiezie-detail-header-bowser':!isWeiXinBowser,'tiezie-detail-header-normal':isWeiXinBowser}">
                  <h1 class="mmnews-detail-title" id="news-title">
                    {{newDetailObj.title}}
                  </h1>
                  <div class="news-detail-tag">
                   <div class="top-newsdetail">
                     <span class="publish-tag" v-show="tags.length>0">来自标签：{{tagName}}</span>
                   </div>
                    <div class="bottom-detail">
                      <span class="publish-time">
                        {{newDetailObj.created_at}}
                    </span>
                      <span class="publish-name" v-show="isShowSource">
                       来源：{{newDetailObj.source}}
                    </span>

                      <span class="publish-name" v-show="!isShowSource">
                        作者：{{newDetailObj.author}}
                    </span>
                    </div>
                    <a class="shouc-plus" @click="shouC($event)" v-show="!isWeiXinBowser">
                      ＋收藏
                    </a>
                  </div>
                </div>
               <!-- <p class="news-abstract" v-show="newDetailObj.abstract!==''">摘要：{{newDetailObj.abstract}}</p>-->
                <p id="editor" class="para-one" v-html="newDetailObj.body" :class="{'para-one-normal':isWeiXinBowser}">
                    {{newDetailObj.body}}
                </p>
               <!-- <div class="news-pic">
                    <img :src="newDetailObj.image" alt="">
                </div>-->
               <!-- <p class="para-one">
                    电气原理图电气原理图是用来表示电路各电气元器件中导电部件的连接关系和工作原理的电路图。
                </p>-->
                <div class="news-read-zan">
                    <div class="news-read">
                        <span>阅读</span>
                        <span>{{newDetailObj.article_views_count}}</span>
                    </div>
                    <div class="news-zan" @click="putZan(newDetailObj.can_like)">
                        <i class="plus">+1</i>
                        <i class="zan-icon"></i>
                        <span class="zan-num" id="likeNum">{{newDetailObj.article_likes_count}}</span>
                    </div>
                </div>
            </section>
          <div class="news-detail-content-mask" id="news-detail-content-mask">
            <div class="news-detail-more-container">
              <span class="news-detail-more-text" @click="openAllDetail()">点击阅读全文</span>
            </div>
          </div>
        </main>
        <main class="ad-box">
            <div class="ad-title"  v-show="isShowAdv">
                <span class="ad-name">推广</span>
            </div>
            <div class="ad-infos" v-for="adv in advObj" v-if="adv.position === 'behind'">
              <a :href="adv.ad_link" class="ad-link">
                <img :src="adv.image_link" alt="" class="adv-link-icon">
              </a>
            </div>
        </main>
        <main class="ad-box leave-msg-box" id="leave-msg-box">
            <div class="ad-title">
                <span class="ad-name">评论</span>
            </div>
            <div class="leave-msg">
              <!--:to="'/leavemsg/'+newDetailObj.id"-->
              <router-link class="write-msg" :to="'/leavemsg/'+newDetailObj.id">写评论</router-link>
              <div style="font-size: 16px;width: 100%;text-align: center;margin-top: 10px;color: #3e3e3e" v-show="isShowText">赶紧第一个评论吧!</div>
                <ul class="leave-msg-contents">
                  <li class="leave-msg-item" v-for="(cItem,index) in articlesComments" @click="showResDialog($event,cItem.id,cItem.commentator.id,cItem.commentator.personal_info.name,cItem.content,index)" v-show="cItem.status!==0">
                    <img :src="cItem.commentator.personal_info.image.medium" alt="" class="leave-msg-pic">
                    <div class="leave-msg-news-r">
                      <span class="leave-msg-uname">{{cItem.commentator.personal_info.name}}</span>
                      <span class="leave-msg-content">{{cItem.content}}</span>
                      <span class="leave-msg-time">{{cItem.created_at}}</span>
                    </div>
                    <ul>
                      <li class="leave-msg-item reply-msg-item" >
                        <div class="leave-msg-news-r reply-msg-news-r" v-for="replyItem in replyComment" v-if="cItem.id==replyItem.comment_id" @click="showReplyToReply($event,replyItem.comment_id,replyItem.from_uid.id,replyItem.from_uid.id,replyItem.from_uid.personal_info.name,cItem.commentator.id,1,replyItem.content)">
                          <span class="leave-msg-uname reply-msg-uname">{{replyItem.from_uid.personal_info.name}}回复{{replyItem.to_uid.personal_info.name}}</span>
                          <span class="leave-msg-content">{{replyItem.content}}</span>
                          <span class="leave-msg-time">{{replyItem.replyed_at}}</span>
                        </div>
                      </li>
                    </ul>
                  </li>
                </ul>
            </div>
          <div class="news-share">
            <button class="news-share-res" @click="replyNews()">回复</button>
            <button class="news-share-jubao" @click="goJuBaoPage()" v-show="isJuBao">举报</button>
            <button class="news-share-delete" @click="deleteReply()" v-show="isShowDelete">删除</button>
            <div class="news-black-line">

            </div>
            <div class="news-share-footer">
              <button class="news-share_btn">取消</button>
            </div>
          </div>
        </main>
        <div class="show-more-box" id="show-more-box" @click="showMore" v-show="!isShowText">
          <span class="show-more-text">查看更多留言</span>
        </div>
        <main class="look-container" v-show="isShowAllLook">
          <div class="look-box">
            <span class="look-box-text">大家都在看</span>
          </div>
          <div class="news-card-container">
            <router-link :to="'/newsdetail/'+allLookObj.id" class="news-card-link">
              <img v-lazy="allLookObj.image" class="news-card-pic"/>
              <div class="news-card-bottom">
                <span>{{allLookObj.title}}</span>
              </div>
            </router-link>
          </div>
          <div class="news-look-content-container">
            <ul class="news-item-box">
              <li v-for="item in allLookNews" track-by="$index" class="news-item" v-show="item.status===1 || item.status==='1'">
                <router-link  class="newsdetail-item-link" :to="'/newsdetail/'+item.id">
                  <img v-lazy="item.image" alt="" class="newsdetail-item-icon">
                  <div class="news-info-container">
                    <p class="news-title">
                      {{item.title}}
                    </p>
                    <div class="news_time_tag">
                      <span class="news_time">{{item.created_at}}</span>
                      <span class="news_tag">来自标签：{{myTagName}}</span>

                    </div>
                    <div class="news_comment_read">
                      <span class="news_comment">评论{{item.article_comments_count}}</span>
                      <span class="news_read">阅读量{{item.article_views_count}}</span>
                    </div>
                  </div>
                </router-link>
              </li>
            </ul>
          </div>
        </main>
        <div class="back-home-container" @click="goBackToHome()">
          点击查看更多
        </div>
    </div>
</template>
<script>
    import Api from '../../api/api';
    import { MessageBox } from 'mint-ui';
    import {delLike} from '../../api/newsdetail';
    import {getSignInfo} from '../../api/tiezidetail';
    import {cLike} from '../../api/newsdetail';
    import {collect} from '../../api/newsdetail';
    import {delCollect} from '../../api/newsdetail';
    import {dealAll} from '../../api/newsdetail';
    import wx from 'weixin-js-sdk';
    import logUtil from '../../utils/log';
    import {isIOS} from '../../utils/checkPafram';
    export default{
        data(){
          return{
              isShowAdv:false,
              isWeiXinBowser:false,
              isShowDelete:false,
              collectioned:-1,
              liked:-1,
              collection_or_cancel:-1,
              isDelete:false,
              isJuBao:false,
              isShowText:true,
              newDetailObj:{

              },
              advObj:[],
              //评论人
              whoPingLun:'',
              //评论的内容
              whoPingLunContent:'',
              //新闻留言
              articlesComments:[],
              //回复留言
              replyComment:[],
              //标签
              tags:[],
              //评论id
              commentId:'',
              //回复人的id
              fromId:'',
              //评论人的id
              toId:'',
              commentator_id:'',
              //回复人的名字
              fromName:'',
              articleId:-1,
              isShowSource:false,
              tagName:"",
              isShowAll:false,
              isShowAllLook:false,
              allLookNews:[],
              myTagName:"",
              myTags:[],
              allLookObj:{}
          }
        },
        mounted(){
          this.articleId = this.$route.params.newsdetailId;
          this.disableScroll();
          this.preventPageScroll();

          this.isWeiXinBowser = this.isWeiXin();
          if(this.isWeiXin()){
            this.getAsign();
          }else{
            this.transViews();
            this.fetchData(this.$route.params.newsdetailId);
          }
        },
        methods:{
          /**
           * 返回主页
           * */
          goBackToHome(){
            window.location.href = "http://m.ciyigou.com/mobile/#/index";
          },
          openAllDetail(){
            var btn = document.getElementById('news-detail-content-mask');
            var obj = document.getElementById('editor');
            var total_height =  obj.scrollHeight;//文章总高度
            console.log("文章的总高度是:",total_height);
            var show_height = 800;//定义原始显示高度
            if(total_height>show_height){
              btn.style.display = 'block';
              obj.style.height = total_height + 'px';
              btn.style.display = 'none';
            }
          },
          /**
           * 查看更多
            */
          showMore(){
            var btn = document.getElementById('show-more-box');
            var obj = document.getElementById('leave-msg-box');
            var total_height =  obj.scrollHeight;//文章总高度
            var show_height = 146;//定义原始显示高度
            if(total_height>show_height){
              btn.style.display = 'block';
              obj.style.height = total_height + 'px';
              btn.style.display = 'none';
            }else{
              this.showMsg("暂无更多评论内容");
            }
          },
          preventPageScroll(){
              var  imgBox  = document.getElementById("editor");
              var moveX = 0;
              var endX = 0;
              var startX = 0;
            /**
             * 触摸事件开始
             */
            imgBox.addEventListener("touchstart", function(e) {
              var event = e || window.event;
              //记录开始滑动的位置
              startX = event.touches[0].clientX;
            }, false);
            imgBox.addEventListener("touchmove", function(e) {
              var event = e || window.event;
              endX = event.touches[0].clientX;
              //记录移动的位置
              moveX = startX - endX;
              if(Math.abs(moveX)>10){
                event.preventDefault();
              }
//              console.log("moveX",Math.abs(moveX));
            }, false);
          },
          goCommenPage($event,id){
//            /leavemsg/
            this.$router.push({path: `/leavemsg/${id}`, replace: true});
            $event.preventDefault();
          },
          /**
           * 判断是否是微信浏览器打开
           * */
          isWeiXin(){
            //判断是否是微信浏览器
            let ua = window.navigator.userAgent.toLowerCase();
            if (ua.match(/MicroMessenger/i) == 'micromessenger') {
              return true;
            } else {
              return false;
            }
          },
          getAsign(){
            const _this = this;
            //将这个动态的url传递给后端(便于后端进行签名操作)
            let url = encodeURIComponent(window.location.href.split('#')[0]);
            //请求分享获取到的签名信息
            getSignInfo(Api.SIGN,url).then((res)=>{
              _this.initAsign(res);
            },(err)=>{
            });
          },
          /**
           * 初始化签名信息
           * */
          initAsign(res){
            const _this = this;
            if(res.status === 200){
              //从服务器端获取到必要的参数,将必要的信息进行初始化赋值
              let sinObj = res.data.data.detail;
              wx.config({
                debug: false,
                appId: sinObj.appId, // 和获取Ticke的必须一样------必填，公众号的唯一标识
                timestamp:sinObj.timestamp.toString(), // 必填，生成签名的时间戳
                nonceStr: sinObj.nonceStr, // 必填，生成签名的随机串
                signature: sinObj.signature,// 必填，签名，见附录1
                //需要分享的列表项:发送给朋友，分享到朋友圈，分享到QQ，分享到QQ空间
                jsApiList: [
                  'onMenuShareAppMessage','onMenuShareTimeline',
                  'onMenuShareQQ','onMenuShareQZone'
                ]
              });
              wx.ready(function () {
                _this.transViews();
                _this.fetchData(_this.$route.params.newsdetailId);
              });
            }
          },
        /**
          * 禁止分享页面的滑动
        */
         disableScroll(){
           $('.news-share').bind("touchmove",function(e){
             e.preventDefault();
           });

         },
         /**
          * 跳转到举报的页面
          * */
          goJuBaoPage(){
           this.$router.push({path: `/jubao/${this.commentId}/${this.whoPingLun}/${this.whoPingLunContent}`});
          },
          /**
           * 删除评论(对新闻的留言)
           * */
          deleteReply(){
            this.hideMask();
            MessageBox({
              title: '提示',
              message: '确定删除此评论吗?',
              showCancelButton: true
            });
            MessageBox.confirm('确定删除此评论吗?').then(action => {
              let token = localStorage.getItem('token');
              const _this = this;
//              logUtil.printLog("删除帖子当前的索引是:",_this.index);
              if(token !== null || token!==''){
                if(_this.status!==-1||_this.status!==0){
                  _this.$http.put(`${Api.COMMENTNEWS}`,{
                    id:_this.commentId,
                    status:0
                  }).then((res)=>{
                    logUtil.printLog("删除成功返回的infos:",res);
                    if(res.status === 200){
                      _this.showMsg('恭喜您，删除成功!');
                      logUtil.printLog("删除以后:",this.articlesComments.length);
                      _this.$router.go(0);
                    }
                  },(err)=>{
                    _this.showMsg('删除失败，请重试!');
                    _this.$store.dispatch('hideLoading');
                  })
                }

              }else{
                _this.$router.replace({path: '/login'});
              }
            });
          },
          /**
           * 最里面层显示回复的回复层
           */
          showReplyToReply($event,commentId,fromId,toId,fromName,commenter_id,flag,content){
            const _this = this;
            $(".news-share").addClass("news-modal-active");
            _this.showMask();
            $(".news-sharebg-active,.news-share_btn").click(function () {
              _this.hideMask();
            });
            this.commentId = commentId;
            this.fromId = fromId;
            this.toId = toId;
            this.fromName = fromName;
            this.commentator_id = commenter_id;
            this.whoPingLun = fromName;
            this.whoPingLunContent = content;
            if(_this.commentator_id === parseInt(localStorage.getItem('userId'))){
              _this.isJuBao = false;
              _this.isShowDelete = true;
            }else{
              _this.isJuBao = true;
              _this.isShowDelete = false;
            }
            if(flag === 1){
              $event.stopPropagation();
              window.event.cancelBubble = true;
            }
          },
         /**
          *针对评论的回复
          **/
          replyNews(){
           this.hideMask();
           if(this.fromId === ''|| this.fromId===null){
             this.$router.push({path: `/replymsg/${this.$route.params.newsdetailId}/${this.commentId}/${this.commentator_id}`});
           }else{
             this.$router.push({path: `/replytoreply/${this.commentId}/${this.fromId}/${this.toId}/${this.fromName}`});
           }

          },
          dealLike(newsId){
            const _this = this;
            delLike(Api.NEWSZAN,newsId).then((res)=>{
              if(res.status === 200){
                _this.liked = 1;
                let topele=$("#likeNum"),topcurrent = parseInt(topele.text());
                $("#likeNum").css({
                  'color':'#20A0FF'
                });
                $(".plus").show(0).stop(true).animate({"top":-18,opacity:0},500,function(){topele.text(topcurrent + 1);});
                _this.showMsg('点赞成功!');
              }
            },(err)=>{
              _this.$store.dispatch('hideLoading');
            });
          },
          cancleLike(newsId){
            const _this = this;
            cLike(Api.NEWSZAN,newsId).then((res)=>{
              if(res.status === 204){
                _this.liked = 0;
                let topele=$("#likeNum"),topcurrent = parseInt(topele.text());
                $(".plus").show(0).stop(true).animate({"top":-18,opacity:0},500,function(){topele.text(topcurrent - 1);});
                $("#likeNum").css({
                  'color':'#9f9f9f'
                });
                _this.showMsg('已取消点赞!');
              }else{
                _this.showMsg('出现错误，请重试!');
                _this.$store.dispatch('hideLoading');
              }
            },(err)=>{
              _this.$store.dispatch('hideLoading');
            });
          },
          /**
           * 点赞功能
           * */
          putZan(canLike){
            const _this = this;
            let token = localStorage.getItem('token');
            if(token===null || token===''){
              _this.$router.replace({path: '/login', replace: true});
            }
            if(canLike === 0){
                this.showMsg('你是作者,无法对自己进行此操作');
                return;
            }
            let newsId = this.$route.params.newsdetailId;
            if(newsId!==null && $.isNumeric(newsId)) {
              switch (this.liked){
                case 0://未点赞,执行点赞操作
                  _this.dealLike(newsId);
                  break;
                case 1://点赞了，执行取消点赞操作
                  _this.cancleLike(newsId);
                  break;
              }
            }
          },
            /**
             * 阅读数,不用对其进行任何的处理
             **/
          transViews(){
              let newsId = this.$route.params.newsdetailId;
              if(newsId!==null && $.isNumeric(newsId)){
                this.$http.post(Api.NEWSVIEW,{
                  post_id:newsId
                }).then((res)=>{

                },(err)=>{

                });
              }

          },
          /**
           * 弹出选择层
           * */
          showResDialog($event,commentId,commentator_id,commentName,commentContent,index){
              const _this = this;
              $(".news-share").addClass("news-modal-active");
                _this.showMask();
              $(".news-sharebg-active,.news-share_btn").click(function () {
                  _this.hideMask();
              });

              _this.commentator_id = commentator_id;
//              console.log("最外面:",_this.commentator_id,localStorage.getItem('userId'),index);
              //同一个人,不能进行举报，可以进行删除操作
              if(_this.commentator_id === parseInt(localStorage.getItem('userId'))){
                  _this.isJuBao = false;
                  _this.isShowDelete = true;
              }else{
                  _this.isJuBao = true;
                  _this.isShowDelete = false;
              }
              _this.commentId = commentId;
              _this.whoPingLun = commentName;
              _this.whoPingLunContent = commentContent;
              $event.stopPropagation();
          },
          /**
           * 显示遮罩层
           * */
          showMask(){
              if ($(".news-sharebg").length > 0) {
                $(".news-sharebg").addClass("news-sharebg-active");
              } else {
                $(".newsdetail-layout").append('<div class="news-sharebg"></div>');
                $(".news-sharebg").addClass("news-sharebg-active");
              }
          },
          /**
           * 隐藏遮罩层
           * */
          hideMask(){
            $(".news-share").removeClass("news-modal-active");
            setTimeout(function () {
              $(".news-sharebg-active").removeClass("news-sharebg-active");
              $(".news-sharebg").remove();
            }, 300);
          },
          /**
           * 显示toast信息
           * */
          showMsg(str){
            this.$toast({
              message: str,
              duration: 2000,
            });
          },
          dealCollect(id){
            const _this = this;
            collect(Api.COLLECTIONS,id).then((res)=>{
              if(res.status === 200 && res.data.data.detail!==null){
                _this.collection_or_cancel = 1;
                $('.shouc-plus').html('已收藏');
                //已经收藏了
                _this.showMsg('收藏成功!');
              }
            },(err)=>{
              _this.showMsg('收藏失败，请重试!');
              _this.$store.dispatch('hideLoading');
            });
          },
          cancleCollect(id){
            const _this = this;
            //这里的取消收藏与删除是不一样的
            delCollect(Api.COLLECTIONS,id).then((res)=>{
              if(res.status === 204){
                _this.collection_or_cancel = 0;
                $('.shouc-plus').html('+收藏');
                _this.showMsg('已取消收藏!');
              }
            },(err)=>{
              _this.showMsg('取消收藏失败，请重试!');
              _this.$store.dispatch('hideLoading');
            });
          },
            /**
             *  收藏新闻
             **/
            shouC($event){

                let token = localStorage.getItem('token');
                const _this = this;
                if(token){
//                  console.log("token:not null",token);
                    if(this.$route.params.newsdetailId!==null || this.$route.params.newsdetailId!=='' || this.$route.params.newsdetailId!=='undefine'){
                        switch(_this.collection_or_cancel){
                          case 0: //还没有收藏,调用收藏的方法
                            _this.dealCollect(this.$route.params.newsdetailId);
                            break;
                          case 1: //已经收藏,调用取消收藏的方法
                            _this.cancleCollect(this.$route.params.newsdetailId);
                            break;
                        }
                    }
                }else{
                  this.$router.push({path: '/login', replace: true});
                }
                $event.preventDefault();
            },
          /**
           * 获取新闻详情数据
           * @param id
           */
            fetchData(id){
              const that = this;
              dealAll(Api.NEWS,Api.NEWSCOMMENTINFOS,Api.ADS,id,Api.AllLook).then(this.$http.spread(function (acct,perms,three,four){
                  //acct对应第一个请求返回的信息,perms对应第二个请求返回的信息
                    that.initNewsInfo(acct);
                    that.initCommentsInfo(perms);
                    that.initAdsInfo(three);
                    that.initAllLookInfo(four);

              }),(err)=>{
                  that.showMsg('获取数据失败，请重试');
                  that.$store.dispatch('hideLoading');
                });
            },
          /**
           * 大家都在看
           * */
            initAllLookInfo(four){
             const that = this;

            if(four.status === 200 && four.data!==null){
              this.isShowAllLook = true;
              this.allLookNews = four.data.data.detail;
              const newsLen = this.allLookNews.length;
              newsLen>0 ? this.isShowAllLook = true : this.isShowAllLook = false;
              for(let i = 0; i<newsLen;i++){
                that.myTags = that.allLookNews[i].tags;

              }
              this.allLookObj = that.allLookNews[0];
//              console.log("获取到大家都在看的信息是:",four,this.allLookObj);
              this.myTagName = "";
              let len = this.myTags.length;
              for(let i=0;i<len;i++){
                if(i<=2){
                  if(i+1===len){
                    if(i>=2){
                      this.myTagName+=this.myTags[i].name+"......";
                    }else{
                      this.myTagName+=this.myTags[i].name;
                    }

                  }else {
                    this.myTagName+=this.myTags[i].name+"、";
                  }
                }
              }
            }else{
                this.isShowAllLook = false;
            }
            },
            initAdsInfo(three){
                if(three.status === 200 && three.data!==null){
                    this.advObj = three.data.data.detail;
                    for(let item of this.advObj){
                        if(item.position === 'behind'){
                          this.isShowAdv = true;
                        }else{
                          this.isShowAdv = false;
                        }
                    }
                }
            },
            initNewsInfo(acct){
              const _this = this;
              logUtil.printLog("新闻中心的数据是:",acct);
              if(acct.status === 200 && acct.data!==null){
                this.newDetailObj = acct.data.data.detail;
                this.isShowAll = true;
                var str = window.location.href;
                var regEn1 = /[?]/im;
                if(regEn1.test(str) ) {
                  var arr = str.split("?")[0];
                  var arr1 = str.split("?")[1].split("#")[1];
                  str = arr+"#"+arr1;
                }
                if(_this.newDetailObj.title.length>0 || _this.newDetailObj.title.length<=17){
                  $("#news-title").height(32);
                }
                if(_this.newDetailObj.title.length>17){
                  $("#news-title").height(64);
                }
                setTimeout(function () {
                  var btn = document.getElementById('news-detail-content-mask');
                  var obj = document.getElementById('editor');
                  var total_height =  obj.scrollHeight;//文章总高度
//                  console.log("文章的总高度是:",total_height);
                  var show_height = 800;//定义原始显示高度
                  if(total_height>show_height){
                    btn.style.display = 'block';
                    obj.style.height = show_height + 'px';
                  }else{
                    btn.style.display = 'none';
                  }

                },0);
                wx.onMenuShareTimeline({
                  title: _this.newDetailObj.title, // 分享标题
                  link: str, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                  imgUrl: _this.newDetailObj.image, // 分享图标
                  success: function (res) {
                    // 用户确认分享后执行的回调函数
                    logUtil.printLog("分享到朋友圈成功返回的信息为:",res);
                    _this.showMsg("分享成功!")
                  },
                  cancel: function (res) {
                    // 用户取消分享后执行的回调函数
                    logUtil.printLog("取消分享到朋友圈返回的信息为:",res);
                  }
                });
                //分享给微信朋友
                wx.onMenuShareAppMessage({
                  title: _this.newDetailObj.title, // 分享标题
                  desc: _this.newDetailObj.abstract, // 分享描述
                  link: str, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
                  imgUrl: _this.newDetailObj.image, // 分享图标
                  type: '', // 分享类型,music、video或link，不填默认为link
                  dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
                  success: function (res) {
                    // 用户确认分享后执行的回调函数
                    logUtil.printLog("分享给朋友成功返回的信息为:",res);
                  },
                  cancel: function (res) {
                    // 用户取消分享后执行的回调函数
                    logUtil.printLog("取消分享给朋友返回的信息为:",res);
                  }
                });
                //分享到QQ
                wx.onMenuShareQQ({
                  title: _this.newDetailObj.title, // 分享标题
                  desc: _this.newDetailObj.abstract, // 分享描述
                  link: str, // 分享链接
                  imgUrl: _this.newDetailObj.image, // 分享图标
                  success: function (res) {
                    // 用户确认分享后执行的回调函数
                    logUtil.printLog("分享到QQ好友成功返回的信息为:",res);
                  },
                  cancel: function (res) {
                    // 用户取消分享后执行的回调函数
                    logUtil.printLog("取消分享给QQ好友返回的信息为:",res);
                  }
                });

                //分享到QQ空间
                wx.onMenuShareQZone({
                  title: _this.newDetailObj.title, // 分享标题
                  desc: _this.newDetailObj.abstract, // 分享描述
                  link: str, // 分享链接
                  imgUrl: _this.newDetailObj.image, // 分享图标
                  success: function (res) {
                    // 用户确认分享后执行的回调函数
                    logUtil.printLog("分享到QQ空间成功返回的信息为:",res);
                  },
                  cancel: function (res) {
                    // 用户取消分享后执行的回调函数
                    logUtil.printLog("取消分享到QQ空间返回的信息为:",res);
                  }
                });
                if(this.newDetailObj.source===""){
                    this.isShowSource=false;

                }else{
                  this.isShowSource=true;
                }
                this.collectioned = this.newDetailObj.collectioned;
                this.liked =  this.newDetailObj.liked;
                this.tags =  this.newDetailObj.tags;
                let len = this.tags.length;
                this.tagName = "";
                for(let i=0;i<len;i++){
                  if(i<=2){
                    if(i+1===len){
                        if(i>=2){
                          this.tagName+=this.tags[i].name+"......";
                        }else{
                          this.tagName+=this.tags[i].name;
                        }

                    }else {
                      this.tagName+=this.tags[i].name+"、";
                    }
                  }
                }

                this.collection_or_cancel =  this.newDetailObj.collection_or_cancel;
                this.filterData(this.newDetailObj.body,this.newDetailObj.source);
                if(this.collection_or_cancel ===1){
                  $('.shouc-plus').html('已收藏');
                }else{
                  $('.shouc-plus').html('+收藏');
                }
                //当数据加载完后，自动返回到顶部
                setTimeout(function () {
                  $('html,body').scrollTop(0);
                },0)
              }else{
                this.isShowAll = false;
                this.showMsg('获取数据失败，请重试');
                this.$store.dispatch('hideLoading');
              }
            },
          /**
           * 替换操作：将获取到的html标签中的img标签进行跳板的方式显示
           * @param content
           */
          filterData(content,source){
            $(function () {
              if(source !=="磁易购"){
                $("#editor").html(content).find("img").each(function () {
                  let getImgs = $(this).data("src").split("?")[0];
                  $(this)[0].src =getImgs;
                 /* `http://read.html5.qq.com/image?src=forum&q=5&r=0&imgflag=7&imageUrl=${getImgs}?=&${Math.random()}`;*/
                })
              }
            })
          },
            initCommentsInfo(perms){
              this.replyComment = [];
              if(perms.status ===200 && perms.data.data.detail!==null){

                this.articlesComments = perms.data.data.detail;
                console.log("获取到评论的信息:",this.articlesComments);
                let len = this.articlesComments.length;
                if(len>=1){
                  this.isShowText = false;
                }else{
                  this.isShowText = true;
                }
//                leave-msg-box
                for(let j=0;j<len;j++){
                  let kLen = this.articlesComments[j].reply_comment.length;
                  for(let k = 0;k<kLen;k++){
                    this.replyComment.push(this.articlesComments[j].reply_comment[k]);
                  }
                }
              }
            },
        },
      /**
       * 使得页面再次刷新
       */
      watch:{
          $route(to){
            //对传递的id进行验证(防止篡改)
            let reg=/newsdetail\/\d+/;
            const _this = this;
            if(reg.test(to.path)){
              this.hideMask();
              let newsId=this.$route.params.newsdetailId || 0;
//              this.$router.go(0);
              if(this.isWeiXin()){
                this.getAsign();
              }else{
                this.transViews();
                this.fetchData(newsId);
              }
            }
          }
        },
    }
</script>
<style scoped>
    @import "../../assets/css/newsdetail.css";
    body {
        background-color: #fff;
    }
</style>



